.statList {
    list-style: none;
    display: flex;
    
}
.percentage {
    display: flex;
    margin-top: 15px;
}
.item {
    padding: 10px;
    width: 50px;
}
.item:nth-child(1) {
    background-color: palevioletred;
}
.item:nth-child(2) {
    background-color: palegoldenrod;
}
.item:nth-child(3) {
    background-color: paleturquoise;
}
.item:nth-child(4) {
    background-color: palegreen;
}
.item:nth-child(5) {
    background-color: rgb(125, 86, 163);
}

